<template>
  <div
    v-if="skillInfo"
    @click="selectNode"
    class="skill-tree-node"
    :class="{'active': active}">
    <img 
      :src="skillInfo.icon" 
      :alt="skillInfo.name"
      class="skill-icon">
  </div>
</template>

<script>
import service from '../services/DataService';


export default {
  name: "SkillTreeNodeView",
  props: ['active', 'skillTreeNode'],
  data() {
    return {
      skillInfo: undefined,
    };
  },
  methods: {
    selectNode(){
      this.active = !this.active;
      this.$emit('nodeSelect')
    },
  },
  mounted() {
    this.skillInfo = service.findSkill(this.skillTreeNode.skillInfoId)
  }
};
</script>

<style>

.skill-icon{
  width: 25px;
  height: 25px;
  opacity: 0.7;
  cursor: pointer;
}

.skill-tree-node.active .skill-icon{
  opacity: 1;
}

</style>
