<template>
  <div class="team-border">
    <div class="team">
      <div
        v-for="(member, idx) in team"
        :key="member.id"
        class="monster-container">
        <monsterView 
          @dataChange="handleChange"
          :monsterData="member"
          :selectedSkills="teamSkills[idx]"/>
      </div>
    </div>
  </div>
</template>

<script>
import Monster from '../model/monster';
import TeamData from '../model/team-data';
import monsterView from './Monster.vue';
import router from '../router'
import service from '../services/DataService';

import LZString from 'lz-string';


export default {
  components: { monsterView },
  name: "team-list",
  data() {
    return {
      teamSize: 6,
      team: [],
      teamSkills: [],
    };
  },
  
  methods: {
    handleChange(){
      const monsterDatas = [];
      for (let i = 0; i < this.team.length; i++) {
        const member = this.team[i];
        const teamData = new TeamData()
        if (member.id >= 0 ) {
          teamData.id = member.id;
          teamData.knowSkills = this.teamSkills[i]
        }else{
          teamData.id = -1;
        }
        monsterDatas.push(teamData)
      }

      const json = JSON.stringify(monsterDatas);
      const compress = LZString.compressToBase64(json)
      router.push({ path: 'team', query: { data: compress }})
    }
  },
  
  beforeMount() {
    let data = this.$route.query.data;
    let teamDatas = undefined;
    if (data) {
      const json = LZString.decompressFromBase64(data)
      teamDatas = JSON.parse(json);
    }
    for (let i = 0; i < this.teamSize; i++) {
      let monster = new Monster();
      let selectedSkills = []
      if (teamDatas && teamDatas.length > i && teamDatas[i] && teamDatas[i].id >= 0) {
        monster = service.findMonster(teamDatas[i].id)
        selectedSkills = teamDatas[i].knowSkills;
      }else{
        monster.id = -1 * (i+1)
      }
      this.team.push(monster);
      this.teamSkills.push(selectedSkills);
    }
  }
};
</script>

<style>
.team {
  display: flex;
  flex-wrap: wrap;
  font-family: 'VT323', monospace;
}

.monster-container {
  background: #334454;
  width: 30%;
  height: 220px;
  border-image: url(https://vignette.wikia.nocookie.net/monster-sanctuary/images/2/25/BlueBorder.png) 14 14 stretch;
  border-width: 14px;
  border-style: solid;
  padding: 5px;
  justify-content: space-between;
  margin-right: 10px;
  margin-bottom: 5px;
  margin-top: 5px;
  color: #fff;
}
</style>
